@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@400;500;800&family=Open+Sans:ital,wght@0,300;0,400;0,700;1,400&display=swap);
body{color:#1A1423}h1,h2{color:#1A1423}h3,h4,h5,h6{color:#66666E}*,*:before,*:after{box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;-ms-box-sizing:border-box}body{font-family:'Open Sans', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1,h2,h3,h4,h5,h6{font-family:'Heebo', sans-serif;margin:0}p{margin:0}h1{font-size:3em;line-height:4rem}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,.App{width:100%;height:100%;margin:0;padding:0;overflow:hidden}.whiteBG{background-color:#FFFFFF !important}.initial{background-color:#F0F0F0;height:100vh;width:100vw;transition:background-color 1s}input{font-size:1em;highlight:none;border-radius:0;outline:none}button{background-color:#9AA899;border:none;color:white;padding:15px 32px;text-align:center;text-decoration:none;display:inline-block;font-size:16px;margin-right:30px;box-shadow:0 0px 10px rgba(0,0,0,0.23),0 2px 3px rgba(0,0,0,0.34)}button:hover{box-shadow:0 0px 7px rgba(0,0,0,0.16),0 2px 2px rgba(0,0,0,0.24)}button:active{box-shadow:0 0px 2px rgba(0,0,0,0.09),0 0px 1px rgba(0,0,0,0.16)}

body{color:#1A1423}h1,h2{color:#1A1423}h3,h4,h5,h6{color:#66666E}.canvas{width:100vw;height:100vh}.abs{display:absolute}.noselect{outline:none;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.toolbox{position:absolute;display:inline-block;width:200px;float:left;background-color:#EAEAEA;margin:20px;border-radius:25px;padding:15px;padding-left:10px;padding-right:10px;box-shadow:0 0px 7px rgba(0,0,0,0.16),0 2px 2px rgba(0,0,0,0.24);z-index:1}.toolbox h3{margin-left:5px}.goal-container{position:absolute;display:inline-block;min-width:130px;right:0;text-align:center;background-color:white;margin:20px;border-radius:25px;padding:10px;box-shadow:0 0px 7px rgba(0,0,0,0.16),0 2px 2px rgba(0,0,0,0.24);z-index:1}.goal-container .red{font-weight:bold;color:#1A1423}.goal-container .green{font-weight:bold;color:green}.tool{background-color:#DFDFDA;border-radius:15px;padding:4px;margin-top:5px;padding-left:16px}.tool:hover{background-color:#DADACA;cursor:pointer}.hoverEl{padding:5px;border-radius:10px;cursor:pointer}.hoverEl:hover{background-color:#e2dbbe}.step-text{height:22px;color:#848484;font-family:"Open Sans";font-size:16px;line-height:22px;text-align:center;position:absolute;left:50%;transform:translateX(-50%);bottom:20px}.step-menu{position:absolute;left:50%;transform:translateX(-50%);bottom:53px;box-sizing:border-box;height:73px;line-height:73px;width:278px;border-radius:36.5px;background-color:#EAEAEA;border:1px solid #E6E6E6;box-shadow:0 0 8px 0 rgba(0,0,0,0.28);text-align:center}.step-menu div{display:inline-block;margin-top:2.6px;margin-left:16px}.step-menu div:first-child{margin-left:0}.step-menu .undo-btn.disabled{background-color:#C3CBC2 !important;box-shadow:none !important;cursor:not-allowed !important}.step-menu .undo-btn.disabled .undo-svg svg path{fill:#868686}.step-menu .redo-btn.disabled{background-color:#B7B7B7 !important;box-shadow:none !important;cursor:not-allowed !important}.step-menu .redo-btn.disabled .redo-svg svg path{fill:#868686}.step-menu .undo-btn{width:60px;height:60px;background-color:#A8B4A7;box-shadow:0 2px 4px 0 rgba(0,0,0,0.24);border-radius:50%;position:relative;margin-right:17px;cursor:pointer}.step-menu .undo-btn:hover{box-shadow:0 1px 2px 0 rgba(0,0,0,0.35)}.step-menu .undo-btn .undo-svg{margin-top:0}.step-menu .redo-btn{position:absolute;bottom:3px;right:100px;background-color:#B7B7B7;border-radius:50%;line-height:32px;height:29px;width:29px;box-shadow:0 1px 6px 0 rgba(0,0,0,0.29);cursor:pointer}.step-menu .redo-btn div{margin-top:0}.step-menu .redo-btn:hover{box-shadow:0 0.5px 3px 0 rgba(0,0,0,0.35)}.step-menu .redo-btn .redo-svg{margin-top:0}.dropdown-menu{width:100vw;height:40px;background-color:#EEE;padding-left:15px}.dropdown-menu .menu-title{height:30px;line-height:30px;padding-left:10px;padding-right:10px;border-radius:8px;float:left;font-weight:bold;margin-top:5px;margin-bottom:5px;cursor:pointer;transition:box-shadow 0.2s}.dropdown-menu .menu-title:hover{box-shadow:inset -5px -5px 3px #F5F5F5, inset 5px 5px 3px #DADADA}.dropdown-menu .menu-title:active{box-shadow:inset -5px -5px 3px #F9F9F9, inset 5px 5px 3px #D4D4D4;background-color:#EBEBEB}.fadein-appear{opacity:0.01}.fadein-appear.fadein-appear-active{opacity:1;transition:1.0s}.insertOverlay{border-radius:25px;padding:15px;background-color:white;position:absolute;top:-50%;left:50%;transform:translate(-50%, -50%);box-shadow:0 0.5px 3px 0 rgba(0,0,0,0.35);transition:top 0.4s;z-index:2}.insertOverlay.shown{top:50%}.insertOverlay h1{font-size:2.4rem}.insertOverlay .check{width:inherit;height:inherit;padding-top:10px;padding-bottom:10px}.insertOverlay .buttons{margin-top:20px;float:right}.insertOverlay .buttons button{border-radius:15px;margin-right:4px}.mainCanvas{transition:0.5s filter ease-in-out,  0.5s -webkit-filter ease-in-out !important;background-color:#fff}.mainCanvas.disabled{pointer-events:none;-webkit-filter:brightness(0.5);filter:brightness(0.5);-webkit-backdrop-filter:brightness(0.5);backdrop-filter:brightness(0.5)}

body{color:#1A1423}h1,h2{color:#1A1423}h3,h4,h5,h6{color:#66666E}.formulaTable{table-layout:fixed;width:100%;border-collapse:collapse}.formulaTable .plus{padding-right:5px;padding-left:3px}.formulaTable .plus:before{content:'+'}.formulaTable tr{border:1px solid #DDD;height:40px}.formulaTable td{vertical-align:middle;padding:5px;height:25px;background-color:#FAFAFA}.formulaTable td.interactive{cursor:pointer;color:#848484}.formulaTable td.interactive:hover{color:#545454}.formulaTable td.close{width:30px}h2{padding-top:15px}input{height:25px;width:100%;padding:15px;border-radius:4px;border:1px solid #DDD;max-width:375px}

.floating-window.shown{top:50% !important}.floating-window{font-size:12px;transition:top 1s;position:absolute;height:600px;width:840px;border-radius:25px;top:-50%;left:50%;transform:translate(-50%, -50%);background-color:white;box-shadow:0 4px 10px 0 rgba(0,0,0,0.11);overflow:hidden}.floating-window .toolbar{position:absolute;bottom:0;height:68px;width:100%;border-radius:0 0 25px 25px;background-color:#EAEAEA;display:flex;flex-direction:row-reverse;padding:6px;padding-right:20px;align-items:center}.floating-window .toolbar .svg{display:inline-block;padding-right:10px}.floating-window .toolbar .back{position:absolute;top:8px;left:20px;background:none;color:#848484;box-shadow:none !important}.floating-window .toolbar button{width:126px;border-radius:15px;margin:0;margin-right:6px;margin-top:2px;margin-bottom:2px;box-shadow:0 0 10px 0 rgba(0,0,0,0.1);transition:0.1s;color:white}.floating-window .toolbar button:hover{box-shadow:0 0 6px 0 rgba(0,0,0,0.3)}.floating-window .toolbar button:active{box-shadow:0 0 2px 0 rgba(0,0,0,0.5);color:#CCC}.floating-window .full-width{padding:40px 60px !important}.floating-window .content{padding:20px;height:720px;overflow:auto}.floating-window .content p{font-size:1.3em;line-height:25px}.floating-window .content .column{padding:20px 40px;float:left;width:50%}.floating-window .content:after{content:"";display:block;clear:both;height:80px}.floating-window .content .divider{position:absolute;top:0;left:50%;height:100%;width:1px;background-color:#B7B7B7}.proofField{box-shadow:0 0 4px 0 rgba(0,0,0,0.3);padding:8px;margin-bottom:10px;border-radius:10px;font-weight:bold;color:#555;cursor:pointer;font-size:1.4em}.proofField:hover{color:#6611FF}

